import React from "react";
import NavbarPage from "../Navbar/NavBarPage";
import { Profile, Languages, Skills, About, Education, Portfolio } from "../Profile.js";
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";

const Layout = () => {
  return (
    <MDBContainer>
     <div>
       
        <MDBCol md="12" >
          <NavbarPage />
        </MDBCol>


      <MDBRow>
        <MDBCol md="4" className="fixxx">
          <Profile />
        </MDBCol>
        <MDBCol md="8">
         
          <Portfolio/>
        </MDBCol>
      </MDBRow>

      <MDBRow>
        <MDBCol md="4">
          <Languages />
          <Skills />
        </MDBCol>
        <MDBCol md="8">
          <Education />
        
        </MDBCol>
      </MDBRow></div>
    </MDBContainer>
  );
};

export default Layout;
