class Profile extends React.Component {
  state = {
    wechat: false
  };

  toggler = () => {
    this.setState({
      wechat: !this.state.wechat
    });
  };

  render() {
    return (
      <React.Fragment>
        <MDBCol>
          <MDBCard className="cards" style={{ width: "100%" }}>
            <MDBCardImage
              className="img-fluid"
              src="http://papers.co/wallpaper/papers.co-vd85-scifi-web-by-emilwidlund-dark-pattern-abstract-art-35-3840x2160-4k-wallpaper.jpg"
              waves
            />
            <img
              src={prof}
              width="120x"
              className="rounded-circle profilepic img-fluid"
            ></img>

            <MDBCardBody>
              <MDBCardTitle>Amir Ibraimov</MDBCardTitle>

              <MDBCardText>
              Software Engineer<br></br>
            +7(777)6341393<br></br> <br></br> 
                <MDBCardText>ibraimov.amir1996@gmail.com</MDBCardText>
                <MDBCardText><a href="https://amirq17.github.io">https://amirq17.github.io</a></MDBCardText>
                <hr></hr>
              </MDBCardText>
              <div className="profile_icons">
                <a href="https://github.com/Amirq17/" target="_blank">
                  <i className="fab fa-github iconka"></i>
                </a>
                <a
                  href="https://cn.linkedin.com/in/amiribraimov?trk=profile-badge"
                  target="_blank"
                >
                  <i className="fab fa-linkedin iconka"></i>
                </a>
                <a
                  href="https://api.whatsapp.com/send?phone=77776341393"
                  target="_blank"
                >
                  <i class="fab fa-whatsapp-square iconka"></i>
                </a>
                <a href="#" onClick={this.toggler}>
                  <i class="fab fa-weixin iconka"></i>
                </a>

                <a href="mailto:ibraimov.amir1996@gmail.com" target="_blank">
                  <i class="fas fa-envelope iconka"></i>
                </a>
              </div>
              <hr></hr>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>

        <MDBContainer>
          <MDBModal
            isOpen={this.state.wechat}
            togglr={this.toggler}
            className="centt"
          >
            <MDBModalHeader toggle={this.toggler}>Wechat</MDBModalHeader>
            <MDBModalBody>
              <img src={qr} className="img-fluid"></img>
            </MDBModalBody>
          </MDBModal>
        </MDBContainer>
      </React.Fragment>
    );
  }
}

